/* DoctorDashboard.css */

.doctor-dashboard {
    margin: 20px;
    padding: 20px;
    border: 1px solid #ccc;
    border-radius: 8px;
  }
  
  .doctor_table_das {
    margin-top: 20px;
  }
  
  .doctor_table_das table {
    width: 100%;
    border-collapse: collapse;
  }
  
  .doctor_table_das th, .doctor_table_das td {
    border: 1px solid #8c8c8c;
    padding: 5px;
    text-align: left;
    vertical-align: top;
  }

  
  .doctor_table_das  th {
    background-color: #dfe2f5;
    color: #333;
    text-align: center !important;
    font-size: 12px;
    padding: 20px 5px;
  }

  .doctor_table_das  td{
    width: 200px;
    padding: 0px !important;
  }
  
  .doctor_table_das  ul {
    list-style-type: none;
    padding: 0;
    display: flex;
    flex-direction: column;
    /* row-gap: 10px; */
    /* margin-top: 11px; */
  }

  .doctor_table_das  li {
 border-bottom: 1px solid #a1a1a1;
 padding: 5px;
 

  }

  .iuyr444r{
    display: flex;
    flex-direction: column;
    row-gap: 8px;
    transition: background-color 0.3s ease;
    padding: 12px 5px
}

  .dsewx{

    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  .dsewx select{
    border-radius: 3px;
    width: 100px;
    height: 19px;
    font-size: 11px;
    padding: 0px;
    background-color: #ececec;
  }

  .kjs26 {
    display: flex;
    align-items: center;
