/*  Header_comp csss */
.Header_comp {
    
    background-color: var(--opacityColor);
    color: var(--labelcolor);
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0px 10px;
    box-sizing: border-box;
    height: 60px;
    width: 100%;
    position: relative;
    z-index: 999;

}

.Header_comp_1 {
    width: auto;
}

.Header_comp_icon {
    cursor: pointer;
    display: inline-block;
    animation-duration: 0.4s;

}

.Header_comp_icon.open {
    animation-name: rotateOpen;
}

.Header_comp_icon.close {
    animation-name: rotateClose;
}

@keyframes rotateOpen {
    from {
        transform: rotate(0deg);
    }

    to {
        transform: rotate(90deg);
        /* Adjust the angle as needed */
    }
}

@keyframes rotateClose {
    from {
        transform: rotate(90deg);
        /* Start from the rotated state */
    }

    to {
        transform: rotate(0deg);
    }
}

.Header_comp_2 {
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 10px;
}

.Header_comp_2_img {
    width: 50px;
    height: 50px;
    display: grid;
    place-items: center;
    background-color: transparent;
    overflow: hidden;
}

.Header_comp_2_img img {
    width: 100%;
    height: 100%;
    object-fit: contain;
    mix-blend-mode: initial;
    box-shadow: 0 0 10px rgb(0 0 0 / 31%);
    clip-path: circle(50%);
    aspect-ratio: 3/2;

}

.Header_comp_2 span {
    font-size: clamp(12px, 2vw, 24px);
    font-weight: 600;
}
.Header_comp_3{
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 10px;
}
.Header_comp_3_loc {
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 5px;
    align-items: center;
}

.Header_comp_3_loc_icon {
    font-size: clamp(20px, 1.5vw, 24px);
}

.Header_comp_3_loc_select {
    padding: 3px 10px;
    background-color: var(--ProjectColor);
    font-size: 13px;
    color: var(--labelcolor);
    border: 1px solid white;
    border-radius: 5px;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    outline: 0px;
}
.Header_comp_3_img {
    width: 50px;
    height: 50px;
    border-radius: 50%;
    display: grid;
    place-items: center;
    background-color: #fff;
    box-shadow: 0 0 10px rgb(0 0 0 / 31%);

}

.Header_comp_3_img img {
    width: 80%;
    height: 70%;
    aspect-ratio: 3/2;
    object-fit: contain;
    mix-blend-mode: darken;

}

.toggled_box_header {
    width: 140px;
    padding: 5px;
    display: flex;
    flex-direction: column;
    gap: 10px;
    border-radius: 5px;
    background-color: var(--ProjectColor);
    position: absolute;
    top: 75px;
    right: 0px;
    font-size: 12px;
  }

  @media screen and (max-width: 450px) {
  .name_of_the3{
    display: none;
  }

  .Header_comp_3_loc_select {
    padding: 3px 4px;
    background-color: var(--ProjectColor);
    font-size: 11px;
    color: var(--labelcolor);
    border: 1px solid white;
    border-radius: 5px;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    outline: 0px;

  }
  .Header_comp_3_loc{
    gap: 3px;
  }

  .Header_comp_3_img,.Header_comp_2_img img{
width: 45px;
height: 45px;
  }

}