body,
html {
  height: 100%;
  /* Ensure the body and html take up full viewport height */
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

:root {
  --ProjectColor: #11c8c8;
  --ProjectColorhover: #d5f7f7;
  --Tablerownthchild: rgb(243, 241, 241);
  --labelcolor: black;
  --projectwhite: white;
  --selectbackgroundcolor: #caffff;
  --fontsize: 12px;
  --opacityColor: #02acac;
}

body {
  margin: 0px;
  padding: 0px !important;
  -webkit-user-select: none;
  /* Safari */
  -moz-user-select: none;
  /* Firefox */
  -ms-user-select: none;
  /* Internet Explorer/Edge */
  user-select: none;
  /* Non-prefixed version, currently supported by Chrome and Opera */
  height: 100%;
  /* Ensure body takes up full height */
}

input {
  -webkit-user-select: none;
  /* Safari */
  -moz-user-select: none;
  /* Firefox */
  -ms-user-select: none;
  /* Internet Explorer/Edge */
  user-select: none;
  /* Non-prefixed version, currently supported by Chrome and Opera */
}

input[type="text"]::-webkit-calendar-picker-indicator {
  display: none;
  /* For WebKit browsers like Chrome and Safari */
}

/* Chrome, Safari, Edge, Opera */
input[type="number"]::-webkit-outer-spin-button,
input[type="number"]::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* General styling to match other input fields if necessary */
input[type="number"] {
  appearance: none;
}


input[type="password"]::-ms-reveal, 
input[type="password"]::-ms-clear {
    display: none;
}

input[type="password"]::-webkit-credentials-auto-fill-button {
    display: none;
}
.app_container {
  position: fixed;
  width: 100%;
  height: 100%;
}

.common_center_tag {
  width: 100%;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
}

.common_center_tag span {
  color: grey;
  font-size: clamp(12px, 1.5vw, 24px);
  font-weight: 600;
}

.Main_container_app {
  /* padding: 10px; */
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  gap: 5px;
  overflow-y: auto;
  /* Enable vertical scrolling */
  box-sizing: border-box;
}


/* Hide scrollbar for Webkit-based browsers (Chrome, Safari) */
.Main_container_app::-webkit-scrollbar {
  display: none;
}

/* Hide scrollbar for Internet Explorer, Edge */
.Main_container_app {
  -ms-overflow-style: none;
  /* IE and Edge */
  scrollbar-width: none;
  /* Firefox */
}


.Main_container_app h3 {
  width: 100%;
